export const StatusColorType = {
  red: 'red' as 'red',
  blue: 'blue' as 'blue',
  gray: 'gray' as 'gray',

};

// estimateTime () [StopStatus2~4PlateNumb - 1EstimateTimenull; StopStatus1 EstimateTimenullEstimateTime; StopStatus0EstimateTime]
// : [0:'',1:'',2:'',3:'',4:'']StopStatus: number;
export function statusDefine(status: number, estimateTime: number | null) {
  if (status === 1) {
    return ['', StatusColorType.gray];
  }
  if (status === 2) {
    return ['', StatusColorType.gray];
  }
  if (status === 3) {
    return ['', StatusColorType.gray];
  }
  if (status === 4) {
    return ['', StatusColorType.gray];
  }

  if (estimateTime === null) {
    console.log("", estimateTime, " ,status", status)
    return ['', StatusColorType.gray];
  }
  if (typeof estimateTime === 'number') {
    if (estimateTime < 60) {
      // return [` ${estimateTime}`, "red"];  
      return [``, StatusColorType.red];  //60
    }

    if (estimateTime >= 60 && estimateTime < 3600) {
      const minutes = Math.floor(estimateTime / 60);

      const seconds = estimateTime % 60;
      if (minutes === 1) { // 
        // return [`  ${minutes}${seconds}`, "red"];
        return [` `, StatusColorType.red];
      }
      return [`${minutes}${seconds}`, StatusColorType.blue];
    }

    if (estimateTime >= 3600) {
      const hours = Math.floor(estimateTime / 3600);
      const remainingSeconds = estimateTime % 3600;
      const minutes = Math.floor(remainingSeconds / 60);
      const seconds = remainingSeconds % 60;
      return [`${hours}${minutes}${seconds}`, StatusColorType.blue];
    }
  }
  return ['', StatusColorType.gray];
}

