import { LangType } from "./const";

interface CityData {
  name: string;
  value: string;

}
export const cityData: CityData[] = [
  { name: '', value: 'Taipei' },
  { name: '', value: 'NewTaipei' },
  { name: '', value: 'Taoyuan' },
  { name: '', value: 'Taichung' },
  { name: '', value: 'Tainan' },
  { name: '', value: 'Kaohsiung' },
  { name: '', value: 'Keelung' },
  { name: '', value: 'Hsinchu' },
  { name: '', value: 'HsinchuCounty' },
  { name: '', value: 'MiaoliCounty' },
  { name: '', value: 'ChanghuaCounty' },
  { name: '', value: 'NantouCounty' },
  { name: '', value: 'YunlinCounty' },
  { name: '', value: 'ChiayiCounty' },
  { name: '', value: 'Chiayi' },
  { name: '', value: 'PingtungCounty' },
  { name: '', value: 'YilanCounty' },
  { name: '', value: 'HualienCounty' },
  { name: '', value: 'TaitungCounty' },
  { name: '', value: 'KinmenCounty' },
  { name: '', value: 'PenghuCounty' },
  { name: '', value: 'LienchiangCounty' },
]


export function getCityNameOrValue(value: string | undefined, lang: string | undefined): string | undefined {
  const city = cityData.find((city) => city.value === value);

  if (!city) {
    return undefined;
  }

  return lang === LangType.en ? city.value : lang === LangType.zh ? city.name : undefined;
}